.delete-button {
  background: url('../../../assets/images/trashbox.svg') no-repeat center/cover;
  border: none;
  width: 1rem;
  height: 1rem;
  cursor: pointer;

  &:hover {
    background: url('../../../assets/images/trashbox-danger.svg') no-repeat
      center/cover;
  }

  &:active {
    transform: scale(0.9);
  }
}
